<template>
  <div id="home">
    <heads></heads>
    <div id="content">
      <project-view></project-view>
      <editor></editor>
      <div id="command_line">
        <hint></hint>
        <shell></shell>
      </div>
    </div>
  </div>
</template>
<script>
import Editor from '../Common/editor'
import Header from '../Common/header'
import Shell from '../Common/shell'
import Hint from '../Common/hint'
import Projectview from '../Common/projectview'

export default {
  name: 'Home',
  components: {
    editor: Editor,
    heads: Header,
    shell: Shell,
    hint: Hint,
    'project-view': Projectview
  }
}
</script>

<style>
#home{
  height: 100%;
  width: 100%;
}
#content {
    position: relative;
    width: 100%;
    height: calc(100% - 35px);
}
#command_line{
    position: absolute;
    left: 65%;
    height: 100%;
    width: 35%;
    box-sizing: border-box;
    display: inline-block;
}
</style>



