<template>
  <div id="project_view">
        <p class="func_button">文件视图</p>
    </div>
</template>

<script>
export default {
  name: 'Project_view'
}
</script>

<style>
#project_view{
    position: absolute;
    left: 0;
    height: 100%;
    width: 10%;
    background: rgba(27, 27, 27, 0.96);
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    display: inline-block;
}
.func_button{
    background: rgba(17, 17, 17, 0.4);
    color: #009A61;
    margin: 5px;
    padding: 1px;
    font-size: 14px;
    border: solid #009A61 1px;
    border-radius: 5px;
    width: 70px;
    text-align: center;
}
</style>



